import React from 'react';
import '../../App.css';
import 'moment/locale/zh-cn';
import 'antd/dist/antd.css';
import '../../index.css';
import { Layout, Typography, Tag, Divider } from 'antd';
import { InlineMath, BlockMath } from 'react-katex';
import 'katex/dist/katex.min.css';
import AppHeader from '../../PublicComponent/Header';
import AppFooter from '../../PublicComponent/Footer';
import AppPageHeader from '../../PublicComponent/PageHeader';const { Title, Text, Paragraph } = Typography;
const { Content } = Layout;
function GradientDescentMethods(){
   window.scrollTo(0,0);
   return(
       <Layout>
           <AppHeader select='2'/>
           <Content className='site-layout' style={{ padding: '0 24px', marginTop: 64 }}>
           <AppPageHeader title='GradientDescentMethods'/>
           <div className='site-layout-background' style={{ padding: 16 }}>
              <PostContent/>
           </div>
           </Content>
           <AppFooter/>
        </Layout>
   );
}
export default GradientDescentMethods;
function PostContent(){
 return(<Layout style={{ backgroundColor: 'white', padding: '0'}}>
<div>
        <Tag color="blue">Neural Network</Tag> <Tag color="blue">Artificial Intelligence</Tag><Tag color="blue">Machine Learning</Tag>
</div>
<Divider></Divider>
<Paragraph>
SGD with momentumAdaGrad, RMS Prop,  Adam</Paragraph>

<Title level={2}></Title>
<Paragraph><a href="/#/posts/HowDoNeuralNetworkWork"></a> (Stochastic Gradient Descent, SGD)RMS Prop, AdaGrad (Adaptive Gradient),  Adam(Adaptive Moment Estimation)</Paragraph>
<Title level={3}></Title>
<Paragraph><InlineMath math="\eta"/> </Paragraph>
<Paragraph><InlineMath math="\nabla C"/> </Paragraph>
<Paragraph><InlineMath math="w"/></Paragraph>
<Paragraph><InlineMath math="m_t"/> </Paragraph>
<Paragraph><InlineMath math="v_t"/> </Paragraph>
<Title level={3}></Title>
<Title level={4}>  |  Batch Gradient Descent</Title>
<Paragraph> <InlineMath math="N"/>  <InlineMath math="{x_1, x_2, \cdots x_N}"/> <InlineMath math="{y_1, y_2, \cdots y_N}"/> <InlineMath math="\nabla C_1(x_1, y_1), \nabla C_2(x_2, y_2), \cdots, \nabla C_N(x_N, y_N)"/></Paragraph>
<Paragraph><BlockMath math="
w \leftarrow w + \frac{1}{b}\sum_{i=0}^{b}{\nabla C_i}\cdot \eta
"/></Paragraph>
<Paragraph></Paragraph>
<Title level={4}>  |  Mini-batch Gradient Descent</Title>
<Paragraph><InlineMath math="b"/> batch batch </Paragraph>
<Paragraph><BlockMath math="
w \leftarrow w + \frac{1}{b}\sum_{i=0}^{b}{\nabla C_i}\cdot \eta
"/></Paragraph>
<Title level={4}>  |  Stocastic Gradient Descent</Title>
<Paragraph> batch 1 - </Paragraph>
<Paragraph><BlockMath math="
w \leftarrow w + \nabla C \cdot \eta
"/></Paragraph>
<Title level={4}></Title>
<Paragraph>0</Paragraph>
<Title level={3}></Title>
<Title level={4}>SGD with Momentum  |  </Title>
<Paragraph><InlineMath math="\eta"/></Paragraph>
<Paragraph><BlockMath math="
m_t = m_{t-1} \cdot \eta + \nabla C \cdot (1 - \eta)
"/></Paragraph>
<Paragraph><BlockMath math="
w \leftarrow w + m_t \cdot \eta
"/></Paragraph>
<Paragraph><InlineMath math="\eta"/>0.9 <InlineMath math="\eta"/></Paragraph>
<Title level={3}></Title>
<Paragraph> -  </Paragraph>
<Title level={4}>AdaGrad - Adaptive Gradient </Title>
<Paragraph><Text strong></Text></Paragraph>
<Paragraph><BlockMath math="
v_t = \sum_{i = 0}^{t}{\nabla C_i^2}
"/></Paragraph>
<Paragraph> Adagrad</Paragraph>
<Paragraph><BlockMath math="
w \leftarrow w + m_t \cdot \frac{\eta}{\sqrt{v_t}}
"/></Paragraph>
<Paragraph> AdaGrad RMS Prop</Paragraph>
<Title level={4}>RMS Prop</Title>
<Paragraph>RMS Prop AdaGradRMS Prop  <InlineMath math="\eta_2"/>RMS Prop</Paragraph>
<Paragraph><BlockMath math="
v_t = \eta_2 v_{t - 1} + (1 - eta_2) \nabla C_t^2
"/></Paragraph>
<Paragraph> <InlineMath math="\eta_2"/>0</Paragraph>
<Title level={4}>Adam (Adaptive Momentum Estimation)</Title>
<Paragraph>Adam</Paragraph>
<Paragraph><BlockMath math="
m_t = m_{t-1} \cdot \eta_1 + \nabla C \cdot (1 - \eta_1)
"/></Paragraph>
<Paragraph><BlockMath math="
v_t = v_{t - 1}\cdot \eta_2 + 
abla C_t^2\cdot (1 - \eta_2)
"/></Paragraph>
<Paragraph><BlockMath math="
w \leftarrow w + m_t \cdot \frac{\eta}{v_t}
"/></Paragraph>
<Paragraph>AdamAdamAdam</Paragraph>
</Layout>
);}